import styled from "styled-components";
import React, {useState} from "react";

const Wrapper = styled.section`
  font-size: 24px;
  >ul {
    display: flex;
    > li{
      width: 50%;
      text-align: center; 
      padding: 16px 0;
      position: relative;//与底下的绝对定位搭配
      &.selected::after{
        content: '';
        display: block;
        height: 3px;//伪元素 此处高度变化并不会占体积了
        background: #333;
        position: absolute;
        bottom: 0;
        left: 0;//可不加，但为了修复某些浏览器的bug
        width: 100%;//css绝对定位，宽度会变成0,
      }
    }
  }
`

type Props = {
    value:('-'|'+'),
    onChange:(value:('-'|'+'))=>void;
}
const CategorySection:React.FC<Props> = (props)=>{
    const categoryMap = {'-':'支出','+':'收入'};
    type X = typeof categoryMap;// {     '-': string;     '+': string; }
    type Keys = keyof X;//type Keys = "-" | "+"
    const [categoryList] = useState<Keys[]>(['-','+']);
    // const [category, setCategory] = useState('-')//+ 表示收入 - 表示支出
    // console.log(category);
    const category = props.value;
    return (
        <Wrapper>
            <ul>
                {categoryList.map(c=>
                    <li className={category === c?'selected':''}
                        onClick={()=>{props.onChange(c)}} key={c}
                    >{categoryMap[c]}</li>
                )}
            </ul>
        </Wrapper>
    )
}
export {CategorySection}