import {useEffect, useState} from "react";
import {createId} from "lib/created";
import {useUpdate} from "./useUpdate";

// 1.必须用use开头，因为使用useState的情况必须是在一个React函数组件里，要么是在一个
// 自定义hooks里，然后自定义hooks必须用use开头，这是React规定的
const useTags = () => { // 封装一个自定义Hook
    //你在你的函数里面，使用useState或者useEffect、useRef等，然后返回一些读写的接口暴露到外面去，
    // 这段代码每次调用，都会将tags从undefined初始化为空数组[],所以封装了useUpdate来规避
    const [tags, setTags] = useState<{ id: number; name: string }[]>([]);

    // componentDidMount
    useEffect(()=>{
        let localTags = (JSON.parse(window.localStorage.getItem('tags')||'[]'));
        if(localTags.length === 0){
            localTags = [
                {id: createId(), name: '衣'},
                {id: createId(), name: '食'},
                {id: createId(), name: '住'},
                {id: createId(), name: '行'},
            ];
        }
        setTags(localTags);
    },[])

    useUpdate(()=>{
        // console.log("set item");
        // console.log(JSON.stringify(tags));
        window.localStorage.setItem('tags', JSON.stringify(tags));
    }, tags)

    const findTag = (tagId:number)=> tags.filter(tag => tag.id === tagId)[0];
    const findTagIndex = (id:number)=>{
        let res = -1;
        for (let i=0;i<tags.length;i++){
            if(tags[i].id === id){
                res = i;
                break;
            }
        }
        return res;
    };
    const updateTag = (id:number, {name}:{name:string})=>{
        // const index = findTagIndex(id);
        // // 深拷贝 tags得到克隆
        // const tagsClone = JSON.parse(JSON.stringify(tags));
        // // 把 tagsClone的第index个元素删除，换成{id:id, name:obj.name}
        // tagsClone.splice(index, 1, {id:id, name:obj.name});
        // setTags(tagsClone);

        // 写法二 优化
        setTags(tags.map((tag)=> tag.id === id ? {id, name} : tag ));
    };
    const deleteTag = (id:number)=>{
        // const index = findTagIndex(id);
        // // 深拷贝 tags得到克隆
        // const tagsClone = JSON.parse(JSON.stringify(tags));
        // // 把 tagsClone的第index个元素删除，换成{id:id, name:obj.name}
        // tagsClone.splice(index, 1);
        // setTags(tagsClone);
        // 写法二 优化
        setTags(tags.filter(tag => tag.id !== id));
    };
    const addTag = ()=>{
        const tagName = window.prompt('新标签的名称：');
        if(tagName !== null && tagName !== ''){
            setTags([...tags, {id:createId(),name:tagName}])
            }
        };
    const getName = (id:number)=>{
        const tag = tags.filter(t=>t.id === id)[0]
        return tag?tag.name:'';
    }
    // 2.然后自已的hooks必须返回一个对象，这是TypeScript要求的，不然会TS报错
    return {
        tags,
        addTag,
        setTags,
        findTag,
        findTagIndex,
        updateTag,
        deleteTag,
        getName,
    }
};
export {useTags};
