import styled from "styled-components";
import React from "react";
import {useTags} from "hooks/useTags";

const Wrapper = styled.section`
  background: #FFFFFF;
  padding: 12px 16px;
  flex-grow: 1;
  display: flex;
  flex-direction:column;
  justify-content: flex-end;
  align-items: flex-start;
  > ol{
      margin: 0 -12px;
      > li{
        background: #D9D9D9;
        border-radius: 18px;
        display: inline-block;
        padding: 4px 18px;
        font-size: 14px;
        margin: 8px 12px;
        &.selected{
          background: #f60;
        }
      }
  }
  > button{
    background: none;
    border: none;
    border-bottom: 1px solid #333;
    padding: 2px 4px;
    color: #666;
    margin-top: 8px;
  }
`

// Props类型，告诉React.FC这个组件，它的props除了默认的child之外，还可以拥有Props类型所拥有的属性
// 也就是value:string[]
type Props = {
    value:number[];
    onChange:(selected:number[])=>void;
};

// 告诉TypeScript，这是一个React函数组件
// 变量名 X:类型 React.FC
// 类型还有number、string、boolean、null等等
// React.FC是个类型 类型还可以有个参数<Props>,这个参数跟后面的某些东西关联
const TagsSection:React.FC<Props> = (props)=>{
    const {tags, addTag} = useTags();
    // const [selectedTagIds,setSelectedTags] = useState<string[]>([]);
    const selectedTagIds = props.value;
    const onToggleTag = (tagId:number)=>{
        const index = selectedTagIds.indexOf(tagId);
        if(index >=0){
            // 如果tag已被选中，就复制所有没有被选中的tag，作为新的selectedTag
            props.onChange(selectedTagIds.filter(t => t !==tagId));
        }else{
            props.onChange([...selectedTagIds, tagId]);
        }
    }
    const getClass = (tagId:number)=> selectedTagIds.indexOf(tagId)>=0?'selected':'';
    return (
        <Wrapper>
            <ol>
                {tags.map(tag=>
                    <li key={tag.id} onClick={()=>{onToggleTag(tag.id)}} className={getClass(tag.id)}>{tag.name}</li>
                )}
            </ol>
            <button onClick={addTag}>新增标签</button>
        </Wrapper>
    )
}

export {TagsSection}