// 封装第一个自定义属性，默认是0
import {useEffect, useRef} from "react";

// fn:()=>void 声明fn是一个函数类型，这个函数不管有没有参数，也不管有没有返回值
// useUpdate是功能是：
//                  tasg更新了（就是不包含第一次更新，就是不包含tasg从undefined变成初始值[]的这个过程），就触发这个函数
export const useUpdate =(fn:()=>void, dependency:any[])=>{

    const count = useRef(0);
    // 什么时候更新count? 每次render的时候都更新
    useEffect(()=>{
        count.current +=1;
        // 1 2  第一次 tags是undefined变成空数组[] 第二次 tag是空数组[]变成有内容的数组
        // console.log(JSON.stringify(tags));
        // console.log(count.current);
    })

    // 每次tags变化，就触发一次，[tags]是条件
    useEffect(()=>{
        if(count.current >1){
            fn()
        }
    }, [fn,dependency])
}
