import Layout from "../components/Layout";
import React, {useState} from "react";
import styled from "styled-components";
import {CategorySection} from "./Money/CategorySection";
import {NoteSection} from "./Money/NoteSection";
import {NumberPadSection} from "./Money/NumberPadSection";
import {TagsSection} from "./Money/TagsSection";
import {useRecords} from "../hooks/useRecords";

const MyLayout = styled(Layout)`
  display: flex;
  flex-direction: column;
`
type Category = ('-' | '+');

const defaultFormData = {
    tagIds: [] as number[],
    note: '',
    category: '-' as Category,
    amount: '0',
}
const CategoryWrapper = styled.div`
  background: #c4c4c4;
`

function Money() {
    const [selected, setSelected] = useState(defaultFormData)
    const {addRecord} = useRecords();
    // const {records, addRecord} = useRecords();
    // 过程：
    //      一开始Money组件执行一遍，获得records
    //     第一次我们拿到的records是空的，
    //     然后点击ok，触发submit函数，进行addRecord添加一条记录，
    //     添加record记录，然后React发现这个组件用的records变了，就会触发更新
    //     触发更新，就会让当前这个Money组件再次执行
    //     第二次我们拿到的records就是有一条记录的
    // console.log('records');
    // console.log(records);

    // typeof获取值selected 的类型
    type Selected = typeof selected;
    // Partial<Selected> 只要Selected类型的一部分
    const onChange = (obj: Partial<Selected>) => {
        setSelected({
            ...selected,
            ...obj
        })
    };
    const submit = () => {
        if (addRecord(selected)) {
            alert('保存成功')
            setSelected(defaultFormData);
        }
    };
    return (
        <MyLayout scrollTop={9999}>
            <TagsSection value={selected.tagIds}
                         onChange={(tagIds) => onChange({tagIds})}/>
            <NoteSection value={selected.note}
                         onChange={(note) => onChange({note})}/>
            <CategoryWrapper>
                <CategorySection value={selected.category}
                                 onChange={(category) => onChange({category})}/>
            </CategoryWrapper>
            <NumberPadSection value={selected.amount}
                              onChange={(amount) => onChange({amount})}
                              onOk={submit}/>
        </MyLayout>
    );
}

export default Money;