import React from "react";
import styled from "styled-components";

const Label = styled.label`
  display: flex;
  align-items: center;

  > span {
    margin-right: 16px;
    white-space: nowrap
  }

  > input {
    width: 100%;
    display: block;
    height: 44px;
    background: none;
    border: none;
  }
`

// Props这个类型，继承Input元素类型的属性
type Props = {
    label:string;
} & React.InputHTMLAttributes<HTMLInputElement>
const Input: React.FC<Props> = (props) => {
    const {label, children, ...rest} = props;
    return (
        <Label>
            <span>{props.label}</span>
            {/*受控型*/}
            {/*<input type="text" placeholder="这里添加备注..." value={note} onChange={(e)=>setNote(e.target.value)}/>*/}
            {/*非受控型*/}
            {/*<input type={props.type} placeholder={props.placeholder}*/}
            {/*        defaultValue={props.defaultValue}*/}
            {/*        onBlur={props.onBlur}*/}
            {/*/>*/}
            <input {...rest}/>
        </Label>
    )
}
export {Input};